const AUTH = 'auth';

export function saveAuth(token: { id: string; url: string }) {
  localStorage.setItem(AUTH, JSON.stringify(token));
}
export function getAuth() {
  const authObjJson = localStorage.getItem(AUTH);
  console.log(authObjJson);
  if (authObjJson) return JSON.parse(authObjJson);
  else return null;
}
